<template>
  <div id="app" v-md-theme="'blue'">
    <header class="header">
      <md-toolbar v-md-ink-ripple>
        <div class="container">
          <div class="md-title">Vue-Video-Player</div>
        </div>
      </md-toolbar>
    </header>
    <main class="main">
      <div class="banner">
        <h2 class="title">Vue-Video-Player</h2>
        <h4 class="subtitle">Video Player component for Vue.js(1.X ~ 2.X)</h4>
        <p class="actions">
          <md-button href="https://github.com/surmon-china/vue-video-player" target="_blank" class="md-primary">View on GitHub</md-button>
          <md-button href="https://github.com/surmon-china/vue-video-player/tree/master/examples" target="_blank" class="md-primary">Example Code</md-button>
          <md-button href="http://docs.videojs.com/docs/api/player.html" target="_blank" class="md-primary">Video.Js API</md-button>
          <md-button class="md-primary" @click.native="playAll">Play all video</md-button>
          <md-button class="md-primary" @click.native="pauseAll">Pause all video</md-button>
          <md-button class="md-warn">HI! Star~</md-button>
        </p>
      </div>
      <div class="container">
        <example ref="examples"></example>
      </div>
    </main>
    <footer class="footer">
      <h5 class="footer-content">
        <span><a href="https://github.com/surmon-china/vue-video-player">Vue-Video-Player</a> is maintained by <a href="https://github.com/surmon-china">surmon-china</a>.</span>
      </h5>
    </footer>
  </div>
</template>

<script>
  import Example from './Example.vue'
  export default {
    name: 'app',
    components: {
      Example
    },
    computed: {
      examples() {
        return this.$refs.examples.$children
      }
    },
    methods: {
      playAll() {
        this.examples.forEach(item => {
          item.player && item.player.play()
        })
      },
      pauseAll() {
        this.examples.forEach(item => {
          item.player && item.player.pause()
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
   #app {

    > .main {
      overflow: hidden;

      > .banner {
        overflow: hidden;
        text-align: center;
        background-color: #fafafa;

        > .title {
          height: 1em;
          line-height: 1em;
          text-transform: uppercase;
        }

        > .subtitle {
          font-size: 1.8em;
          text-transform: uppercase;
          color: #a5a5a5;
          margin: 1em 0;
          font-weight: 200;
        }

        > .actions {
          margin-top: 3em;
          margin-bottom: 2em;
        }
      }

      > .container {
        margin: 2em auto;
      }
    }

    > .footer {
      border-top: 1px solid #eee;

      > .footer-content {
        font-size: 1.1em;
        text-transform: uppercase;
        margin: 0;
      }
    }
  }
</style>



